	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/link-working-with-sqlite-in-air-using-orm/index.md absPath of file >>> MarkdownRemark","html":"<p>If you don't know what ORM is read the next paragraph, and it might help.  If you do, skip to the links below to learn a little more about how you can make ORM work with AIR and SQLite.</p>\n<p>ORM stands for <a href=\"http://en.wikipedia.org/wiki/Object-relational_mapping\">Object Relational Mapping</a>, in basic terms the concept of representing your tables-based data as more familiar objects and classes. Not a big concept after all, it's one that I've been using already in my AIR applications - albeit unwittingly and manually.  FlexORM is a project to assist and standardise this process, so it's worth knowing about and trying out.</p>\n<h3>Read more about ORM:</h3>\n<p><a href=\"http://www.adobe.com/newsletters/edge/october2009/articles/article7/index.html\">http://www.adobe.com/newsletters/edge/october2009/articles/article7/index.html</a></p>\n<p><a href=\"http://www.adobe.com/newsletters/edge/december2009/articles/article7/index.html\">http://www.adobe.com/newsletters/edge/december2009/articles/article7/index.html</a></p>","frontmatter":{"title":"Link: Working with SQLite in AIR, with the help of ORM","date":"January 28, 2010","tags":["air","flexorm","orm"]}}},"pathContext":{}}

